.resume {
  background: url(../../assets/left-side-decor.svg) no-repeat center;
  text-align: center;
  .rounded {
    font-family: $font__header;
    max-width: 650px;
    display: block;
    padding: 1em;
    text-align: center;
    text-transform: uppercase;
    text-decoration: none;
    color: white;
    background: $color__button;
    margin: 0 auto;
    margin-bottom: 1rem;
    border-radius: 50px;
    transition: all 1s ease;
    &:hover {
      background: white;
      color: $color__button;
    }
  }
  .email {
    text-decoration: none;
    font-weight: bold;
    transition: color 1s ease;
    color: $color__text-main;
    &:hover {
      color: $color__button
    }
  }
}