.about-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  img {
    max-width: 250px;
  }
  @include tablet {
    justify-content: space-between;
    flex-wrap: nowrap;
    img {
      order: 1;
      margin-left: 1rem;
      margin-bottom: 3rem;
      max-width: 400px;
    }
  }
}


.social-icons {
  list-style: none;
  margin: 0 auto;
  padding: 0;
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 320px;
  @include tablet {
    max-width: 600px;
  }
  a {
    -webkit-transition: all .3s ease-out;
    transition: all .3s ease-out;
    width: 68px;
    height: 68px;
    position: relative;
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
    display: block;
    border-radius: 100%;
    @include tablet {
      width: 125px;
      height: 125px;
    }
  }
  li:hover a{
  -webkit-transform: rotateY(180deg);
  transform: rotateY(180deg);
}
  .front,
  .back {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    position: absolute;
    top: 0;
    left: 0;
    width: 68px;
    height: 68px;
    @include tablet {
      width: 125px;
      height: 125px;
    }
  }
  .front {
    z-index: 2;
  }
  .back {
    z-index: 1;
    -webkit-transform: rotateY(180deg);
    transform: rotateY(180deg);
  }
  .linkedin-front {
    background: url(../../assets/linkedin-icon.svg) no-repeat;
    background-size: contain;
  }
  .linkedin-back {
    background: url(../../assets/linkedin-hover.svg) no-repeat;
    background-size: contain;
  }
  .instagram-front {
    background: url(../../assets/ins.svg) no-repeat;
    background-size: contain;
  }
  .instagram-back {
    background: url(../../assets/ins-hover.svg) no-repeat;
    background-size: contain;
  }
  .medium-front {
    background: url(../../assets/medium.svg) no-repeat;
    background-size: contain;
  }
  .medium-back {
    background: url(../../assets/medium-hover.svg) no-repeat;
    background-size: contain;
  }
}
