.projects {
  padding: 3em 0em 0em;
}

.projects-list {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
  li {
    @include tablet {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      margin-bottom: 125px;
      &:nth-child(even){
        img {
          order: 1;
        }
      }
    }
  }
  .project-info {
    color: white;
    background: $color__button;
    padding: 2em 2em;
    h3 {
      margin: 0;
      text-align: left;
      color: white;
    }
    a {
      font-family: $font__header;
      width: 100%;
      display: block;
      padding: 1em;
      text-align: center;
      text-transform: uppercase;
      border: 2px solid white;
      text-decoration: none;
      color: white;
      border-radius: 50px;
      transition: all 1s ease;
      &:hover {
        background: white;
        color: $color__button;
      }
    }
    @include tablet {
      width: 35%;
      p {
        font-size: 1rem;
      }
    }
  }
    img {
    display: block;
    @include tablet {
      width: 62%;
      height: auto;
    }
  }
}